import {
    MAP_INIT,
    CHANGED_MAP_SOURCE,
    MAPPING_INIT,
    UPDATE_MAP_SIZE,
    SET_SELECT,
    CHANGED_MAP_SELECT
} from '../constants/Map';

export function initMap(source,width,height) {

    return (dispatch) => {
        dispatch({
            type: MAP_INIT,
            payload: {
                source,
                width,
                height,
            },
        });

    }
}

export function changedMapSource(source) {
    return (dispatch) => {
        dispatch({
            type: CHANGED_MAP_SOURCE,
            payload: source,
        });
    }
}

export function initMapping(mapping) {
    return (dispatch) => {
        dispatch({
            type: MAPPING_INIT,
            payload: mapping,
        });
    }
}

export function updateMapSize(newWidth, newHeight) {
    return (dispatch) => {
        dispatch({
            type: UPDATE_MAP_SIZE,
            payload: {newWidth, newHeight},
        });
    }
}

export function selectCells(value) {
    return (dispatch) => {
        dispatch({
            type: SET_SELECT,
            payload: value,
        });
    }
}

export function changedMapSelect(newItem) {
    console.log('Action', newItem);
    return (dispatch) => {
        dispatch({
            type: CHANGED_MAP_SELECT,
            payload: newItem,
        });
    }
}
export function initMapSelect(array) {
    console.log('Action', newItem);
    return (dispatch) => {
        dispatch({
            type: 'INIT_MAP_SELECT',
            payload: array,
        });
    }
}

